.header {
  display: flex;
  flex-direction: column;
}
.icon {
  border-radius: 50%;
  width:150px;
  margin-left: 580px;
  margin-top: -300px;
  border: 3px solid var(--color);
}
.header .heading {
  font-size: 2.7rem;
  font-weight: 500;
  text-transform: capitalize;
  margin-top: -50px;
}

.header .subHeading {
  color: var(--color);
  font-weight: 500;
  font-size: 1.1rem;
}

.header .links {
  margin-top: 15px;
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  row-gap: 10px;
}

.header .link {
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}

.header .link svg {
  color: var(--color);
  height: 16px;
  width: 16px;
}

.main {
  display: flex;
  gap: 30px;
}

.col1 {
  flex: 1.3;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.col2 {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.section {
  padding: 15px;
  background-color: rgba(50,50,50,0.1);
  border-radius: 10px;
}
.section .sectionTitle {
  font-size: 1rem;
  color: var(--color);;
  font-weight: bold;
  width: 100%;
  padding: 10px;
  border-bottom: 1.5px solid rgba(50,50,50,0.3);

}
.sectionImg {
  font-size: 1.4rem;
  font-weight: bold;
  width: 250px;
  padding: 20px;
  border-radius: 10px;
  background-color: var(--color);
}

.section .content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
}
.section .content .item {
  border-bottom: 1px dotted lightgray;
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-bottom: 10px;
}
.section .content .item:last-child {
  border-bottom: none;
}

.content .title {
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.3rem;
}

.content .subTitle {
  font-weight: bold;
  color: var(--color);
  font-size: 1rem;
}

.content .overview {
  font-size: 0.875rem;
}

.content .link {
  display: flex;
  gap: 5px;
  font-size: 0.75rem;
  cursor: pointer;
  color: var(--color);
}
.content .link svg {
  height: 14px;
  width: 14px;
  color: var(--color);
}

.content .date {
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 0.875rem;
}

.content .date svg {
  height: 14px;
  width: 14px;
}

.content .points,
.numbered {
  padding-left: 18px;
  font-size: 0.875rem;
}

.numbered li {
  list-style-type: decimal;
}

.hidden {
  display: none;
}
